.highmovies__container {
  margin: 0px 10px;
}

.highmovies__container:first-child {
  margin-left: 0px;
}

.highmovies__container:last-child {
  position: relative;
  margin-right: 20px;
  flex-shrink: 0;
}

.highmovies__container:last-child::after {
  content: none;
  position: absolute;
  right: -10px;
  width: 10px;
  height: 100%;
  background-color: #3c3c47;
}

img {
  width: 120px;
  height: 160px;
  object-fit: cover;
  border-radius: 4px;
}

h5 {
  width: 120px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.rate__container {
  display: flex;
  align-items: center;
}

.rate__container > p {
  margin-left: 4px;
  font-size: 15px;
  font-weight: 400;
  color: #9e9ea4;
}

.highmovies__title {
  font-size: 15px;
  font-weight: 400;
}
